import React, { Component } from 'react';
import { withRouter } from 'react-router-dom'
import Menu from 'react-burger-menu/lib/menus/pushRotate';
import _ from 'lodash';
import './App.css';
import './Menu.css';

class App extends Component {
  constructor() {
    super();
    this.state = {
      isMenuOpen: false,
      openedTools: [],
      tools: [{
        type: 'Grid',
        author: 'Layoutit',
        url: 'https://www.layoutit.com/grid'
      },{
        type: 'Keyframe Animation',
        author: 'CSS Animate',
        url: 'https://cssanimate.com/'
      },{
        type: 'Keyframe Animation',
        author: 'TheAppGuruz',
        url: 'https://www.theappguruz.com/tag-tools/web/CSSAnimations/'
      },{
        type: 'Border Radius',
        author: 'CSSmatic',
        url: 'https://www.cssmatic.com/border-radius'
      },{
        type: 'Gradient',
        author: 'CSSmatic',
        url: 'https://www.cssmatic.com/gradient-generator'
      },{
        type: 'Box Shadow',
        author: 'CSSmatic',
        url: 'https://www.cssmatic.com/box-shadow'
      },{
        type: 'Flexbox',
        author: 'loading.io',
        url: 'https://loading.io/flexbox/'
      },{
        type: 'SASS',
        author: 'SassMeister',
        url: 'https://www.sassmeister.com/'
      }]
    };
    // sort tools
    _.each(this.state.tools, (tool)=>{ tool.id = this.generateToolId(tool) });
    this.state.tools = _.sortBy(this.state.tools, 'id');
    // initialize
    this.state.toolTypes = this.getToolTypes(this.state.tools);
    console.log(this.state.tools);
    console.log(this.state.toolTypes);
  }

  componentWillMount() {
    this.openTool(this.props.location.pathname.replace(/\//g, ''));
  }

  generateToolId(tool) {
    return `${tool.type}-${tool.author}`.replace(/\s/g, '-').toLowerCase();
  }

  getToolTypes(tools) {
    return _(tools).uniqBy('type').map('type').value();
  }

  getToolById(tools, toolId) {
    return _.find(tools, (tool)=>(tool.id===toolId));
  }

  openTool(toolId, e) {
    if(e) {
      e.preventDefault();
    }
    let tool = this.getToolById(this.state.openedTools, toolId);
    if(!tool) {
      tool = this.getToolById(this.state.tools, toolId);
      this.state.openedTools.push(tool);
    }
    this.setState({
      currentTool: tool,
      isMenuOpen: false
    });
    this.props.history.push('/' + tool.id)
  }

  render() {
    return (
      <div id="outer-container">
        <Menu pageWrapId={ "page-wrap" } outerContainerId={ "outer-container" } isOpen={ this.state.isMenuOpen }>
        {this.state.tools.map((tool, i) => <a key={i} onClick={(e)=>this.openTool(tool.id, e)} className="bm-item" href="javascript:;">{`${tool.type} - ${tool.author}`}</a>)}
        </Menu>
        <main id="page-wrap">
          {this.state.openedTools.map((tool, i) => <iframe key={i} src={tool.url} title={tool.author} className={"tool-window "+(tool.id===this.state.currentTool.id ? 'active' : '')} >{tool.author}</iframe>)}
        </main>
      </div>
    );
  }
}

export default withRouter(App);



